

//banner

.faq-banner{
    
    height: 304px;
    background-image: url(../images/72.png);
    @include tablet{
        background-image: url(../images/72-768.png);
        background-position: center bottom;
        
    }
    @include mobile-lg{
        background-image: url(../images/72-375.png);
        height: 143px;
        
    }
}


.faq-slogan{
    color: $dark;
    justify-content: flex-start;
    align-items: flex-end;

    h2{
        font-weight: 500;
        font-size: 64px;
        margin-bottom: 16px;
        
    }
    img{
        margin-left: 10px;
    }
    
    @include tablet{
        h2{
            font-size: 48px;
            margin-bottom: 9px;
            
        }
        img{
            width: 48px;
            height: 48px;
            margin-left: 16px;
            align-self: center;
        }
    }
    @include mobile-lg{
        h2{
            font-size: 28px;
            margin-bottom: 8px;
            
        }
        img{
            width: 24px;
            height: 24px;
            margin-left: 8px;
        }
    }
}

.faq-content{
    padding-top: 88px;
    padding-bottom: 156px;
    @include tablet{
        padding-top: 40px;
        padding-bottom: 104px;
    }
    @include mobile-lg{
        padding-top: 24px;
        padding-bottom: 56px;
    }
}
.faq-title{
    margin-bottom: 16px;
    font-weight: 500;
    font-size: 32px;
    @include tablet{
        font-size: 28px;
        margin-bottom: 32px;
    }
    @include mobile-lg{
        font-size: 20px;
        margin-bottom: 16px;
    }
}
.question{ 
    h3{
        padding: 22px 38px 22px 38px;
        border: 1px solid #000000;
        border-bottom: 0;
        font-weight: 600;
        font-size: 20px;
        cursor: pointer;
    }
    p{  
        padding: 22px 38px 22px 38px;
        background: #F2F2F2 0% 0% no-repeat padding-box;
        border: 1px solid #000000;
        border-bottom: 0;
        display: none;
    }
    li:nth-child(5){
        border-bottom: 1px solid #000;
    }
    @include tablet{
        h3{
            font-size: 16px;
            padding: 16px 24px 16px 24px;
        }
        p{
            font-size: 14px;
            padding: 16px 24px 16px 24px;
        }
    }
    @include mobile-lg{
        h3{
            padding: 16px 16px 16px 16px;
        }
        p{
            padding: 16px 16px 16px 16px;
        } 
    }
}
.question{
    .up{
        margin-left: auto;
        display: none;
    }
    
    .down{
        margin-left: auto;
    }
    h3:hover{
        background-color: $primary;
        color: #fff;
    }
    h3.active{
        background-color: $primary;
        color: #fff;
        .up{
            display: block;
        }
        .down{
            display: none;
        }
    }
}
