

// nav
.menu{
    padding: 30px 0;
    justify-content: space-between;
    @include mobile-lg{
        justify-content: center;
        padding: 10px 0;
    }
    @include mobile{
        justify-content: center;
        padding: 10px 0;
    }
}

.logo{
    margin-left: 88px;
    a{
        background-image: url(../images/logo.png);
        width: 80px;
        height: 49px;
        display: block;
        text-indent: 101%;
        overflow: hidden;
        white-space: nowrap;
    }
    @include tablet{
        margin-left: 48px;
    }
    @include mobile-lg{
        margin-left: 0;
    }
}
.menu-group{
    align-items: center;
    a{
        text-decoration: none;
        color: $white;
        font-size: 20px;
        display: block;
        // margin-right: 64px;
        padding-left: 32px;
        padding-right: 32px;
    }
    @include tablet{
        a{
            margin-right: 48px;
        }
    }
    
}
.showmenu{
    display:none;
    @include mobile-lg{
        display:block;
        position:absolute;
        top:13px;
        left: 16px;  
        text-decoration: none;
        font-size:30px;
        color: #fff;
        &:hover{
            cursor: pointer;
            opacity: 0.5;
        }
    }

}
//漢堡選單
.menu-group{
    @include mobile-lg{
        flex-direction: column;
    /*隱藏選單開始*/
        max-height:0px;
        overflow:hidden;
    /*隱藏選單結束*/
        transition: max-height 2.3s;
        margin-top: 1px;
    
    /*絕對定位疊在網頁上*/
        position: absolute;
        z-index: 100;
        top: 69px;
        left: 0;
        right: 0;
        background: $primary;    
        li{
            width: 100%;
            border: 1px solid #fff;
            
            a{
                color: #fff;
                padding: 16px 0;
                margin-right: 0;
                text-align: center;
                &:hover{
                    background: $secondary;
                }
            }
        }
    }    
}

.dropdown.active {
    max-height: 256px;
}


//footer
.footer{
    padding-top: 48px;
    padding-bottom: 27px;
    padding: 48px 24px 27px 24px;
    @include tablet{
        padding-top: 40px;
        padding-bottom: 40px;
        padding: 40px 0 40px 0;
    }
    @include mobile-lg{
        padding-top: 24px;
        padding-bottom: 20px;
        padding: 24px 0 20px 0;
    }
}

.link{
    justify-content: space-between;
    margin-bottom: 18px;
    .menu-group{
        a{
            margin-right: 48px;
        }
    }
    .community-group{
        a{
            display: block;
            margin-left: 32px;
        }
    }
    @include tablet{
        margin-bottom: 22px;
        .menu-group{
            a{
                font-size: 16px;
                margin-right: 40px;
            }
        }
        .community-group{
            a{
                margin-left: 24px;
                width: 24px;
            }
        }
    }
    @include mobile-lg{
        .menu-group{
            display: none;
        }
        .community-group{
            margin-left: auto;
        }
    }
}
.info{
    margin-bottom: 24px;
    border-bottom: 1px solid #fff;
    .info-group{
        font-size: 36px;
        font-family: Roboto;
        color: $white;
        li:nth-child(1){
            margin-bottom: 16px;
            img{
                margin-right: 23px;
                height: 36px;
                align-self: center;
            }
        }
        li:nth-child(2){
            margin-bottom: 24px;
            img{
                margin-right: 12px;
                height: 48px;
            }
        }
    }
}
.info{
    margin-bottom: 16px;
    @include tablet{
        .info-group{
            font-size: 20px;
            li:nth-child(1){
                margin-bottom: 11px;
                img{
                    width: 13px;
                    height: 13px;
                    margin-right: 8px;
                }
            }
            li:nth-child(2){
                margin-bottom: 24px;
                img{
                    width: 20px;
                    height: 20px;
                    margin-right: 5px;
                }
            }    
        }    
    }
    @include mobile-lg{
        transform: translateY(-48px);
        .info-group{
            font-size: 16px;
            li:nth-child(1){
                margin-bottom: 8px;
                img{
                    width: 23px;
                    height: 23px;
                    margin-right: 12px;
                }
            }
            li:nth-child(2){
                margin-bottom: 24px;
                img{
                    width: 32px;
                    height: 32px;
                    margin-right: 8px;
                }
            }  
        }
    }
}

.copyright{
    color: $white;
    @include mobile-lg{
        transform: translateY(-48px);
        height: 48px;
    }
}
.copyright-group{     
    li:nth-child(1){
        margin-right: 38px;
    }
    li:nth-child(2){
        font-family: Roboto;
        margin-right: auto;
        align-self: center;
    }
    li:nth-child(3){
        margin-right: 62px;
        font-size: 14px;
        align-self: center;
    }
    li:nth-child(4){
        font-size: 14px;
        align-self: center;
        img{
            display: none;
        }
    }
}
.copyright-group{
    @include tablet{
        li:nth-child(1){
            display: none;
        }
        li:nth-child(3){
            margin-right: 24px;
        }
    }
    @include mobile-lg{
        flex-direction: column;
        li:nth-child(1){
            display: none;
            
        }
        li:nth-child(2){
            font-size: 14px;
            margin-bottom: 8px;
        }
        li:nth-child(3){
            margin-bottom: 8px;
            margin-right: auto;
        }
        li:nth-child(4){
            display: flex;
            justify-content: space-between;
            align-self: auto;
            img{
                display: block;
                width: 46px;
                height: auto;
            
            }
        }
    }
}