body {
    font-family: BlinkMacSystemFont, "Segoe UI", "Microsoft JhengHei", Roboto, "Helvetica Neue", Arial, sans-serif;
    font-size: 16px;
    line-height: 1.5;
    font-weight: 400;
}
a{
    text-decoration: none;
}

//容器
.container{
    max-width: 1200px;
    margin: 0 auto;
    @include tablet{
        max-width: 848px;
    }
    @include pad{
        max-width: 672px;
    }
    @include mobile-lg{
        max-width: 352px;
    }
    @include mobile{
        max-width: 352px;
    }
}


// 共用

.bg-primary{
    background:$primary;
}
.bg-base{
    background-color: $base;
}
.bg-cover {
    background-size: cover;
    background-position: center center;
}
.d-flex{
    display: flex;
}
.flex-column{
    flex-direction: column;
}
.flex-row-reverse{
    flex-direction: row-reverse;
}
.align-items-center{
    align-items: center;
}


.title{
    text-align: center;
    font-size: 48px;
    font-weight: 600;
    @include tablet{
        font-size: 32px;
    }
    @include pad{
        font-size: 32px;
    }
    @include mobile-lg{
        font-size: 24px;
    }
    @include mobile{
        font-size: 24px;
    }
}
.d-none{
    display: none;
}
.ml-auto{
    margin-left: auto;
}
.h-100{
    height: 100%;
}


@media (max-width:1920px){
    .mr-lg-8{
    margin-right: 8px;
    
    }
    .mb-lg-8{
    margin-bottom: 8px;
    }
}

@media(max-width:996px) and (min-width:768px){
    .mb-md-3{
        margin-bottom: 3px;
    }
    .mr-md-3{
        margin-right: 3px;
    }
    .mb-md-0{
        margin-bottom: 0;
    }
    .d-md-none{
        display: none;
    }
}
@media(max-width:767px) and (min-width:375px){
    .mb-sm-2{
        margin-bottom: 2px;
    }
    .mr-sm-2{
        margin-right: 2px;
    }
    .d-sm-none{
        display: none;
    }
    .d-sm-block{
        display: block;
    }
    
}