.classic-list{
    max-width: 1200px;
    margin: 0 auto;
    li{
        width: 400px;
        justify-content: center;
        border-bottom: 1px solid #DCDCDC;
        border-left: 1px solid #DCDCDC;
        padding-top: 24px;
        padding-bottom: 24px;
        position: relative;
        &:hover:after{
            content: '';
            position: absolute;
            top: 87px;
            left: 0;
            width: 400px;
            height: 16px;
            background: $primary;
        }
    }
    li.active:after{
        content: '';
        position: absolute;
        top: 87px;
        left: 0;
        width: 400px;
        height: 16px;
        background: $primary;
    }
    img{
        margin-right: 8px;

    }
    a{
        font-weight: 500;
        font-size: 42px;
        font-family: Roboto;
        line-height: 1.3;
        cursor: pointer;
        color: #000;
        
    }
    @include tablet{
        justify-content: center;
    }
}
.classic-list li{
    @include tablet{
        width: 256px;
        padding-top: 16px;
        padding-bottom: 16px;
        &:hover:after{
            content: '';
            top: 61px;
            left: 0;
            width: 256px;
            height: 8px;
            // opacity: 0.5;
            // display: none;
        }
        &.active:after{
            content: '';
            top: 61px;
            left: 0;
            width: 256px;
            height: 8px;
        }
        a{
            font-size: 28px;
        }
    }
    @include mobile-lg{
        width: 125px;
        padding-top: 8px;
        padding-bottom: 8px;
        &:hover:after{
            content: '';
            top: 31px;
            left: 0;
            width: 125px;
            height: 4px;
            // display: none;
        }
        &.active:after{
            content: '';
            top: 31px;
            left: 0;
            width: 125px;
            height: 4px;
        }
        a{
            font-size: 14px;
        }
    }
}
.classic-list{
    li:nth-child(1){
        img{
            width: 48px;
            height: 37px;
            align-self: center;
        }
    }
    li:nth-child(2){
        img{
            width: 48px;
            height: 48px;
        }
    }
    li:nth-child(3){
        border-right: 1px solid #DCDCDC;
        img{
            width: 35px;
            height: 48px;
        }
    }
    @include tablet{
        li:nth-child(1){
            img{
                width: 32px;
                height: 25px;
            }
        }
        li:nth-child(2){
            img{
                width: 32px;
                height: 32px;
            }
        }
        li:nth-child(3){
            img{
                width: 23px;
                height: 32px;
            }
        }
    }
    @include mobile-lg{
        li:nth-child(1){
            img{
                width: 15px;
                height: 11px;
            }
        }
        li:nth-child(2){
            img{
                width: 14px;
                height: 15px;
            }
        }
        li:nth-child(3){
            img{
                width: 11px;
                height: 15px;
            }
        }
    }
}

.tab-content {
    .tab-pane {
        display: none;
    }
    .active {
        display: block;
    }
}


.classic-banner{
    justify-content: center;
    li{
        width: 49%;
    }
    li:nth-child(1){
        margin-right: 2%;
    }
    @include tablet{
        li{
            width: 100%;
        }
        li:nth-child(1){
            margin-right: 0;
        }
    }
}

.classic-content{
    padding-top: 88px;
    padding-bottom: 156px;
    h2{
        font-size: 42px;
        line-height: 1.3;
        font-family: Roboto;
        font-weight: 500;
        text-align: center;
    }
    h3{
        text-align: center;
        font-weight: 300;
        font-size: 32px;
        margin-bottom: 48px;
    }
}
.classic-content{
    @include tablet{
        padding-top: 40px;
        padding-bottom: 104px;
        h2{
            font-size: 32px;
        }
        h3{
            font-size: 24px;
            margin-bottom: 32px;
        }
    }
    @include mobile-lg{
        padding-top: 24px;
        padding-bottom: 56px;
        h2{
            font-size: 20px;
        }
        h3{
            font-size: 16px;
            margin-bottom: 20px;
        }
    }
}
.classic-content-group{
    flex-wrap: wrap;
    margin-left: -12px;
    margin-right: -12px;
    > li{
        // width: 23.5%;
        padding-right: 12px;
        padding-left: 12px;
        margin-bottom: 104px;
    }
    @include tablet{
        justify-content: center;
        > li{
            margin-bottom: 72px;
        }

    }
    @include mobile-lg{
        > li{
            margin-bottom: 32px;
        }
    }
}

.card-body{
    span{
        font-size: 24px;
        font-family: Roboto;
    }
    span:nth-child(1){
        color: $dark;
    }
    span:nth-child(2){
        color: $primary;
        font-weight: 500;
        margin-left: auto;
    }
    @include mobile-lg{
        span{
            font-size: 16px;
        }
    }
}


.color-list{
    li{
        width: 24px;
        height: 24px;
        border-radius: 4px;
        margin-right: 8px;
    }
    li:nth-child(1){
        background: #5F3E2D;
    }
    li:nth-child(2){
        background: #B75929;
    }
    @include mobile-lg{
        li{
            width: 16px;
            height: 16px;
            margin-right: 5px;
        }
    }
}

.pagination{
    justify-content: center;
    margin-top: -16px;
    @include tablet{
        margin-top: -19px;
    }
    @include mobile-lg{
        margin-top: 8px;
    }
}
.page-item.active .page-link {
    z-index: 2;
    color: #fff;
    background-color: #000;
    border-color: #555555;
}
.page-item.disabled .page-link {
    color: #555555;
    pointer-events: none;
    background-color: #f2f2f2;
    border-color: #555555;
}
.page-link{
    position: relative;
    display: block;
    padding: 10px 12px 12px 12px;
    // margin-left: -1px;
    color: #555555;
    background-color: #ffffff;
    border: 1px solid #555555;
}
.page-item .page-link {
    border-right: 0;
}
.page-item:last-child .page-link {
    border-right: 1px solid #555555;
}

.sunglasses .classic-group {
    .card-body{
        span:nth-child(2){
            margin-left: 0;
        }
    }

}

//圖片
.banner-pc{
    @include pad{
        display: none;
    }
}
.banner-pad{
    display: none;
    @include pad{
        display: block;
    }
    @include mobile{
        display: none;
    }
}
.banner-mobile{
    display: none;
    @include mobile{
        display: block;
    }
}

.card-img-pc{
    @include tablet{
        display: none;
    }
}
.card-img-tablet{
    display: none;
    @include tablet{
        display: block;
    }
    @include mobile-lg{
        display: none;
    }
}
.card-img-mobile{
    display: none;
    @include mobile-lg{
        display: block;
    }
}