// 變數
$primary:#AA0601;
$secondary:#650300;
$base: #AA06010D;
$white:#fff;
$dark:#000;
$gray-dark:#212529;
$gray:#707070;
$gray-light:#dee2e6;



//@Mixin
@mixin tablet{
    @media(max-width:992px){
        @content;
    }
}
@mixin pad{
    @media(max-width:768px){
        @content;
    }
}
@mixin mobile-lg{
    @media(max-width:767px){
        @content;
    }
}
@mixin mobile{
    @media(max-width:375px){
        @content;
    }
}
