//classic
.classic{
    padding: 64px 0 80px 0;
    @include tablet{
        padding: 40px 0;
    }
    @include mobile-lg{
        padding: 24px 0;
    }
}
.classic-title{
    color: $secondary;
    margin-bottom: 60px;
    @include tablet{
        margin-bottom: 32px;
    }
    @include mobile-lg{
        margin-bottom: 16px;
    }
}

.classic-group-item>img{
    @include tablet{   
        max-width: 410.85px;
        
    }
    @include mobile-lg{
        max-width:352px;

    }
}
.classic-group-item{
    margin-bottom: 32px;
    h2{
        color: $primary;
        font-weight: 900;
        font-style: italic;
        font-size: 48px;
        font-family: Roboto;
        align-items: center;
        margin-left: 24px;
    }
    &:nth-child(3){
        margin-bottom: 0;
    }
    &:nth-child(2){
        h2{
            margin-right: 24px;
        }
    }
    @include tablet{   
        margin-bottom: 8px;
        &:nth-child(2){
            flex-direction: row;
        }
        h2{
            font-size: 28px;
            justify-content: center;
            width: 50%;
        }
    }
    @include mobile-lg{
        justify-content: center;
        position: relative;
        h2{
            position: absolute;
            top: 51px;
            left: -25px;
            color: $white; 
            width: 100%;
        }
    }


}
.classic-group-item:nth-child(1){ 
    h2{
        &:before{
        content: '';
        background-image: url(../images/iconfinder_44_2529968.svg);
        margin-right: 8px;
        width: 48px;
        height: 37px;
        
        }
    }
    @include tablet{
        h2{
            &::before{
                content: '';
                width: 32px;
                height: 25px;
                background-size: cover;
            }
        }
    }
    @include mobile-lg{
        h2{
            &::before{
                content: '';
                background-image: url(../images/iconfinder_44_2529968-w.svg);

            }
        }
    }

}
.classic-group-item:nth-child(2){
    h2{
        &:before{
        content: '';
        background-image: url(../images/Layer_1.svg);
        margin-right: 8px;
        width: 48px;
        height: 48px;
        
        }
    }
    @include tablet{
        h2{
            &::before{
                content: '';
                width: 32px;
                height: 32px;
                background-size: cover;
            }
        }
    }
    @include mobile-lg{
        h2{
            &::before{
                content: '';
                background-image: url(../images/Layer-1-w.svg);
                
            }
        }
    }
}
.classic-group-item:nth-child(3){
    h2{
        &:before{
        content: '';
        background-image: url(../images/Page-1.svg);
        margin-right: 8px;
        width: 35px;
        height: 48px;
        }
    }
    @include tablet{
        h2{
            &::before{
                content: '';
                width: 23px;
                height: 32px;
                background-size: cover;
            }
        }
    }
    @include mobile-lg{
        h2{
            &::before{
                content: '';
                background-image: url(../images/Page-1w.svg);
                
            }
        }
    }

}

