//more按鈕
.btn-white{
    color: $primary;
    background-color: $white;
}
.more-btn{
    display: block;
    text-decoration: none;
    padding: 5px 19px 5px 32px;
    display: flex;
    position: absolute;
    top: 18.5%;
    left: 503px;
    font-weight: 700;
    font-size: 72px;
    line-height: 1.32;
    font-family: Roboto;

    .btn-img{
        align-self: center;
        margin-left: 17px;
        transform:translateX(0) ;
        transition: transform 0.5s;
    }
    &:before{
        content: 'MORE';
        position: absolute;
        top: 4px;
        left: 24px;
        transform:translateY(0) ;
        transition: transform 0.6s;
        opacity: 0.1;
    }
    &:after{
        content: 'MORE';
        position: absolute;
        top: 4px;
        left: 24px;
        transform:translateY(0) ;
        transition: transform 0.6s;
        opacity: 0.1;
    }
    &:hover{
        .btn-img{
            transform:translateX(15px) ;
            transition: transform 0.5s;
        }
        &:before{
            transform:translateY(-120px) ;
            transition: transform 0.5s;
            opacity: 0.4;
        }
        &:after{
            transform:translateY(-75px) ;
            transition: transform 0.5s;
            opacity: 0.7;
        }
    }
    @media (max-width:1300px){
        font-size: 56px;
        padding: 4px 16px 4px 24px;
        top: 20px;
        left: 377px;
        .btn-img{
            margin-left: 12px;
            width: 36px;
        }
    }
    @media (max-width:1200px){
        top: 16px;
        left: 350px;
    
    }
    @include tablet{
        font-size: 40px;
        padding: 3px 10px  3px 19px;
        top: 20px;
        left: 297px;
        .btn-img{
            margin-left: 10px;
            width: 27px;
            
        }
        &:before{
            display: none;

        }
        &:after{
            display: none;
        }
    }
    @include pad{
        font-size: 40px;
        padding: 3px 10px  3px 19px;
        top: 10px;
        left: 178px;
        .btn-img{
            margin-left: 10px;
            width: 27px;
            
        }
        &:before{
            display: none;
        }
        &:after{
            display: none;
        }
    }
    @include mobile-lg{
        font-size: 26px;
        padding: 4px 14px 3px 12px;
        top: 32px;
        left: 10px;
        .btn-img{
            margin-left: 8px;
            width: 18px;
        }
    }
    @include mobile{
        font-size: 26px;
        padding: 4px 14px 3px 12px;
        top: 32px;
        left: 10px;
        .btn-img{
            margin-left: 8px;
            width: 18px;
        }
    }

}


