.form-check{
    font-family: Roboto;
    margin-bottom: 40px;
    .form-check-input {
        margin-right: 4px;
    }
    .form-check-label{
        align-self: center;
    }
    @include pad{
        margin-bottom: 24px;
    }
}
.form-check{
    input[type=checkbox] {
	-moz-appearance:none;
	-webkit-appearance:none;
	-o-appearance:none;
	outline: none;
	content: none;	
    }
    input[type=checkbox]:before {
        content: "";
        display: block;
        width: 24px;
        height: 24px;
        border: 1px solid black;
        
    }
    input[type=checkbox]:checked:before {
        background-image: url(../images/check-solid.svg);
        background-repeat: no-repeat;
        font-size: 36px;
        color: #000;
    }
}
.form-control{
    display: block;
    width: 100%;
    height: calc(1.5em + 0.75rem + 2px);
    padding: 14px 16px;
    font-size: 16px;
    font-weight: 400;
    line-height: 24px;
    color: $dark;
    background-color: $base;
    background-clip: padding-box;
    border: 1px solid $dark;
    font-family: Roboto;
}



.btn-primary {
    color: #fff;
    background-color: $primary;
    &:hover {
    background-color: $secondary; 
    }
}
.form-btn {
    display: block;
    font-weight: 700;
    user-select: none;
    padding: 16px 58px;
    font-size: 16px;
    cursor: pointer;
    align-self: center;
    width: 180px;
    @include pad{
        width: 136px;
        padding: 7px 36px;
    }
}



